.title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  margin-bottom: 6px;
}

.member {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.avatar {
  border-radius: 50%;
  width: 30px;
}

.name {
  padding: 0 12px;
  color: #fff;
  font-size: 14px;
}

.time {
  color: #70e5f9;
  margin: 0;
  padding-top: 2px;
}

.list {
  counter-reset: myCounter;
  padding: 0;
  margin: 0;
}

.item {
  list-style: none;
  line-height: 20px;
  margin: 0;
  padding-left: 24px;
  position: relative;
  margin-left: 12px;
}

.item:before {
  position: absolute;
  left: -12px;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  counter-increment: myCounter;
  content: counter(myCounter);
  color: #445cc7;
  background-color: #27bdeb;
  display: inline-block;
  text-align: center;
  line-height: 1.5;
}

.item:not(:last-child) {
  border-left: 1px dashed #27bdeb;
}

.description {
  margin: 0;
  color: #fff;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 1px;
}



