.room {
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  padding: 10px 0;
  cursor: pointer;
  -ms-flex-align: center;
      align-items: center;
  border-left: 5px solid transparent;
  position: relative;
}

  .room:hover {
    background-color: #ededed;
  }

.roomSelected {
  /* border-left-color: #f59123; */
  background-color: #e0e0e0;
}

.roomSelected:hover {
    background-color: #e0e0e0;
  }

.roomSelected:after{
    content: '';
    position: absolute;
    right: -10px;
    top: 50%;
    margin-top: -10px;
    width: 20px;
    height: 20px;
    background: #fff;
    -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }

.image {
  width: auto;
  height: 50px;
}

.roomName {
  padding-left: 15px;
  opacity: 0.6;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: left;
  color: #4a4a4a;
}

.selected {
  opacity: 1;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: left;
  color: #333;
}

.roomImageContainer {
  display: block;
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 50px;
}

@media screen and (max-width: 1200px) {
  .room {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .roomName {
    padding-left: 0;
    font-size: 14px;
  }
}



