.modal {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}

  .modal .content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 500px;
    height: auto;
    outline: none;
  }

  .modal .content .title {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: end;
          justify-content: flex-end;
      background-color: white;
      border-radius: 4px 4px 0 0;
    }

  .modal .content .title .closeButton {
        cursor: pointer;
        padding: 0 5px;
        color: #666;
        font-size: 24px;
        -webkit-transition: .4s ease all;
        -o-transition: .4s ease all;
        transition: .4s ease all;
      }

  .modal .content .title .closeButton:hover {
          color: #ed1c24;
          -webkit-transition: .4s ease all;
          -o-transition: .4s ease all;
          transition: .4s ease all;
        }



