.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-top: 24px;
}

  .row .additionalElements {
    font-size: 14px;
  }

  .row .additionalElements a {
      color: #455cc7;
    }

  .row .additionalElements a:hover {
        color: #28bdeb;
      }

  .row .additionalElementsBottom {
    margin-bottom: 0;
  }

.title {
  font-size: 24px;
  font-weight: 300;
  text-align: center;
  margin: 8px 0;
}

.email,
.password,
.names {
  display: block;
  width: 100%;
  height: 32px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 8px;
}

input[type="email"],
input[type="password"] {
  background-color: #fff;
  outline: none;
}

input[type="checkbox"] {
  margin-right: 8px;
}

.text {
  display: block;
  font-size: 13px;
  margin-top: 24px;
}

.link {
  margin-left: 8px;
}

.error {
  border: 2px solid red;
}


