.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
}

  .container .headerContainer {
    height: 60px;
  }

  .container .mainPage {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    height: calc(100% - 60px);
  }

  .container .mainPage .roomSelectorBlock {
      display: -ms-flexbox;
      display: flex;
      width: 260px;
    }

  .container .mainPage .appointmentBlock {
      display: -ms-flexbox;
      display: flex;
      -ms-flex: 1 1;
          flex: 1 1;
      margin-right: 1px;
    }

  .container .mainPage .datePickerBlock {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
      width: 320px;
      background-color: #455cc7;
      padding: 10px 0;
    }

  .container .mainPage .datePickerBlock .datePicker {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: center;
            justify-content: center;
        height: 280px;
        border-bottom: 1px solid #0f0e9f;
      }

  .container .mainPage .datePickerBlock .meetings {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex: 1 1;
            flex: 1 1;
        padding: 5px 0 0 5px;
        overflow-y: auto;
      }

  .container .modal {
    background: grey;
  }



