.roomSelectorContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #f3f3f3;
  padding-left: 5px;
}

  .roomSelectorContainer .header {
    padding: 20px;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.5px;
    text-align: left;
    color: #4a4a4a;
  }

.emptyRoomsContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #f3f3f3;
  width: 100%;
}

.emptyRoomsContainer i {
    font-size: 48px;
    color: #28bdeb;
  }

@media screen and (max-width: 1200px) {
    .roomSelectorContainer .header {
      text-align: center;
    }
}



