.button {
  width: auto;
  height: 36px;
  line-height: 36px;
  border: 0;
  border-radius: 4px;
  padding: 0 24px;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: .7px;
  color: #fff;
  background-color: #e71;
  cursor: pointer;
  -webkit-transition: .4s ease all;
  -o-transition: .4s ease all;
  transition: .4s ease all;
  text-transform: uppercase;
}

.button:hover {
  background-color: #f90;
  -webkit-transition: .4s ease all;
  -o-transition: .4s ease all;
  transition: .4s ease all;
}



