.account {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: relative;
  height: 60px;
}

.info {
  display: -ms-flexbox;
  display: flex;
  padding: 3px 10px 3px 6px;
  cursor: default;
}

.avatar {
  width: 40px;
  height: 40px;
  margin: 6px;
}

.image {
  width: 100%;
}

.dropdown {
  position: relative;
}

.dropdown:hover > .dropdownMenu {
  display: block;
}

.dropdown:hover {
  background-color: #f0f0f0;
}

.dropdownMenu {
  background-color: #fff;
  display: none;
  text-align: right;
  position: absolute;
  top: 60px;
  right: 0;
  padding: 0;
  margin: 0;
  width: 120px;
  z-index: 2;
}

.dropdownItem {
  cursor: pointer;
  padding: 1em;
  text-align: left;
  list-style-type: none;
}

.dropdownItem:hover {
  background-color: #70e5fa;
  color: #445cc7;
  -webkit-transition: 0.5s ease;
  -o-transition: 0.5s ease;
  transition: 0.5s ease; 
}



