.container {
  background-color: #fff;
  padding: 32px;
  border-radius: 2px;
  width: 518px;
}

  .container .title {
    font-size: 24px;
    font-weight: 300;
    text-align: center;
    margin: 8px 0;
  }

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.row .additionalElements {
    font-size: 14px;
  }

.row .additionalElements a {
      color: #455cc7;
    }

.row .additionalElements a:hover {
        color: #28bdeb;
      }

.row .additionalElementsBottom {
    margin-bottom: 0;
  }

.formHeader {
  text-align: center;
}

.email,
.password {
  display: block;
  width: 100%;
  height: 32px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 8px;
  border: solid 1px #d4d4d4;
}

.emailContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}

.emailContainer .email {
    width: 60%;
  }

.emailContainer .wavetag {
    padding-left: 5px;
    width: 40%;
  }

input[type="text"],
input[type="password"] {
  background-color: #fff;
  outline: none;
}

input[type="checkbox"] {
  position: relative;
  top: 2px;
  margin-right: 8px;
}

.text {
  display: block;
  font-size: 13px;
  margin-top: 24px;
}

.link {
  margin-left: 8px;
}

.error {
  border: 2px solid red;
}



